<template>
    <div>
        <!-- <br>
        <h1>个人中心</h1> -->
        <br>
            <template v-if="!isLogining">
                <p>账号：<input v-model="account" type="text" name="account" value="账号"></p>
                <p>密码：<input v-model="password" type="password" name="password" value="密码"></p>
                <p v-if="!isLegal" style="color:red">登录错误</p>
                <input type="submit" value="登录" @click.prevent="judgeIsLegal();">
                <!-- <button>注册</button> -->
            </template>
            <template v-if="isLogining">
                <p>{{ account }}, 已登录</p>
                <button v-on:click.prevent="setIsLogining(false);setLocalStorageData();">退出登录</button>
            </template>
    </div>
</template>

<script>
import { mapState } from "vuex"
import store from "@/store/index.js"

export default {
    data(){
        return{
            account: "",
            password: "",
        }
    },
    computed:{
        //组件中使用仓库的数据
        ...mapState("login", ["data", "isLogining", "isLegal", ]),
    }, 
    methods:{
        setData(){
            store.commit("login/setData", {});
        },
        setIsLogining(param){
            store.commit("login/setIsLogining", param);
        },
        setIsLegal(param){
            store.commit("login/setIsLegal", param);
        },
        judgeIsLegal(){
            if (!this.account || !this.password){
                return;
            }
            var legal = this.account.length >= 4 && this.password.length >= 4
            this.setIsLegal(legal)
            this.setIsLogining(legal)
            var payload = {}
            if (legal) { 
                payload = {account:this.account, password:this.password} 
            }
            this.setData(payload);
            var loginData = JSON.stringify(payload)
            window.localStorage.setItem("loginData", loginData)
        },
        getLocalStorageData(){
            var payload = window.localStorage.getItem("loginData")
            var loginData = JSON.parse(payload) || []
            this.account = loginData.account;
            this.password = loginData.password;
        },
        setLocalStorageData(){
            this.password = "";
            var payload = {account:this.account, password:this.password} 
            this.setData(payload);
            var loginData = JSON.stringify(payload)
            window.localStorage.setItem("loginData", loginData)
        },
    },
    created(){     
        this.getLocalStorageData()
        this.judgeIsLegal()
    },
}
</script>

